select {
	padding: 6.3px 12px;
	border-radius: 11px;
	border: 1px solid #c4cdd5;
	background-color: #ffffff;
	font-weight: bold;
	color: #919eab;
}

canvas {
	font-family: "Nexa", sans-serif;
	font-size: 14px;
}

h1 {
	font-size: 2.25rem;
}

.nav {
	min-height: 100vh;
	padding: 2rem 1rem;
	background-color: #f2f8ff;
}

@media screen and (min-width: 375px) {
	.nav__mobile {
		display: flex;
	}
	.nav__logo.active {
		display: none;
	}
	.nav {
		transform: translateX(-130%);
		position: absolute;
		left: 61px;
	}
}

@media screen and (min-width: 768px) {
	.nav__mobile {
		display: none;
	}
	.nav {
		transform: none;
		position: static;
	}
}


.toggle-checkbox:checked {
	@apply: right-0 border-green-400;
	right: 0;
	border-color: #68D391;
  }
  .toggle-checkbox:checked + .toggle-label {
	@apply: bg-green-400;
	background-color: #68D391;
  }

  
.nav__mobile {
	min-height: 100vh;
	padding: 1rem 0.5rem;
	background-color: #f9fafb;
	box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.nav__mobile ul li {
	list-style: none;
}

.nav__hamburger {
	width: 45px;
	height: 45px;
	cursor: pointer;
}

.nav__hamburger > span {
	display: block;
	width: 45px;
	height: 5px;
	background-color: #637381;
	transition: all 0.3s;
	border-radius: 3px;
}
.nav__hamburger > span:not(:last-child) {
	margin-bottom: 7px;
}
.nav__hamburger.active > span:first-child {
	transform: rotate(45deg) translateX(8.5px) translateY(9px);
	background-color: #4062ff;
}
.nav__hamburger.active > span:nth-child(2) {
	transform: translateX(-100%);
	opacity: 0;
}
.nav__hamburger.active > span:last-child {
	transform: rotate(-45deg) translateX(8px) translateY(-8px);
	background-color: #4062ff;
}

.nav__logo-min {
	transform: rotate(-90deg);
}

.nav__menus {
	list-style: none;
}

.nav__menu,
.nav__logout {
	font-weight: bold;
	padding: 1rem;
	width: 200px;
	cursor: pointer;
}

.nav__menu {
	color: #b8bed9;
	transition: 0.3s ease-in-out;
}

.nav__menu:hover {
	color: #4062ff;
}

.nav__menu svg,
.nav__logout svg {
	margin-right: 0.5rem;
}

.nav__menu svg path {
	fill: #b8bed9;
	transition: 0.3s ease-in-out;
}

.nav__menu:hover svg path {
	fill: #4062ff;
}

.nav__menu.active {
	color: white;
	background-color: #4062ff;
	border-radius: 16px;
}

.nav__menu.active:hover {
	color: #ffffff;
}

.nav__menu.active svg path {
	fill: #ffffff;
}

.nav__menu.active:hover svg path {
	fill: #ffffff;
}

.nav__logouts {
	flex: 0.2;
	list-style: none;
}

.nav__logout {
	color: #ff4842;
	transition: 0.3s ease-in-out;
}

.nav__logout:hover {
	color: #4062ff;
}

.nav__logout svg path {
	fill: #ff4842;
	transition: 0.3s ease-in-out;
}

.nav__logout:hover svg path {
	fill: #4062ff;
}

.section__main {
	background-color: #fbfdff;
	padding: 2rem;
	width: 100%;
}

.search {
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
	border-radius: 16px;
	width: fit-content;
}

.search__button {
	border: none;
	padding: 0.85rem 1rem 0.85rem 1.5rem;
	border-radius: 16px 0 0 16px;
}

.search__input {
	border: none;
	outline: none;
	border-radius: 0 16px 16px 0;
	font-weight: bold;
	padding-right: 2rem;
}

.banner {
	border-radius: 24px;
}

.banner__cta {
	width: 100%;
}

.banner__img {
	position: relative;
}

.banner__img img {
	position: absolute;
	top: -150px;
	right: 0;
}

.portos {
	display: grid !important;
	grid-template-columns: repeat(auto-fill, minmax(285px, 285px));
	gap: 1rem;
}

.porto {
	font-weight: bold;
	padding: 1rem;
	border-radius: 16px;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.charts {
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
	background-color: #ffffff;
	width: 100%;
	border-radius: 16px;
	padding: 1rem;
}

@media screen and (min-width: 375px) and (max-width: 767px) {
	.section__main {
		padding: 2rem 1rem;
		width: 100%;
	}
	.search {
		width: 100%;
	}
	.search__button {
		padding: 0.85rem 1rem 0.85rem 1rem;
	}
	.search__input {
		padding-right: 1rem;
		width: 100%;
	}
	.banner {
		flex-direction: column-reverse;
		padding: 1rem;
		height: max-content;
		margin-bottom: 2rem !important;
	}
	.banner__cta {
		justify-content: center;
		display: flex;
	}
	.banner__img {
		position: static;
		width: 100%;
	}
	.banner__img img {
		position: static;
		width: 100%;
	}
	h1.text-4xl {
		font-size: 1.75rem;
		text-align: center;
	}
	h2.text-2xl {
		text-align: center;
	}
	.portos {
		margin-bottom: 2rem;
	}
	.portos > .porto:not(:last-child) {
		margin-bottom: 2rem;
	}
	.porto {
		max-width: 285px;
	}
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
	.banner {
		padding: 2rem;
		margin-bottom: 2rem;
	}
	.banner__img img {
		position: absolute;
		top: -108px;
		right: -29px;
		width: 250px;
	}
	.portos {
		margin-bottom: 2rem;
	}
}

@media screen and (min-width: 1200px) {
	.banner {
		margin-bottom: 3.5rem;
		padding: 2.5rem;
		height: 245px;
	}
	.banner__img img {
		position: absolute;
		top: -150px;
		right: 0;
	}
	.portos {
		margin-bottom: 2rem;
	}
}

.section__account {
	background-color: #ffffff;
	padding: 2rem 1rem;
}

.option {
	height: 56px;
}

.notif {
	position: relative;
}

.notif__active {
	position: absolute;
	top: 0;
	right: 0;
}

.profile__pic {
	object-fit: cover;
	border-radius: 50%;
}

.card {
	width: 275px;
	height: 155px;
	border-radius: 24px;
	background: #4062ff;
}

.card p {
	background-color: #4081ff;
	padding: 0.5rem;
	border-radius: 26px;
}

@media screen and (min-width: 375px) {
	.section__account {
		display: none;
	}
}

@media screen and (min-width: 1200px) {
	.section__account {
		display: block;
	}
}

