  height: -webkit-max-content;
  height: max-content;
}

.form {
  background-color: #15172b;
  border-radius: 20px;
  box-sizing: border-box;
  height: -webkit-max-content;
  height: max-content;
  padding: 20px;
  width: 40%;
}

.title {
  color: #eee;
  font-family: sans-serif;
  font-size: 36px;
  font-weight: 600;
  margin-top: 30px;
}

.subtitle {
  color: #eee;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 600;
  margin-top: 10px;
}

.input-container {
  height: 50px;
  position: relative;
  width: 100%;
}

.ic1 {
  margin-top: 40px;
}

.ic2 {
  margin-top: 30px;
}

.input {
  background-color: #303245;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  font-size: 18px;
  height: 100%;
  outline: 0;
  padding: 4px 20px 0;
  width: 100%;
}

.cut {
  background-color: #15172b;
  border-radius: 10px;
  height: 20px;
  left: 20px;
  position: absolute;
  top: -20px;
  -webkit-transform: translateY(0);
  transform: translateY(0);
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
  width: 76px;
}

.cut-short {
  width: 50px;
}

.input:focus ~ .cut,
.input:not(:placeholder-shown) ~ .cut {
  -webkit-transform: translateY(8px);
  transform: translateY(8px);
}

.placeholder {
  color: #65657b;
  font-family: sans-serif;
  left: 20px;
  line-height: 14px;
  pointer-events: none;
  position: absolute;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  transition: color 200ms, -webkit-transform 200ms;
  transition: transform 200ms, color 200ms;
  transition: transform 200ms, color 200ms, -webkit-transform 200ms;
  top: 20px;
}

.input:focus ~ .placeholder,
.input:not(:placeholder-shown) ~ .placeholder {
  -webkit-transform: translateY(-30px) translateX(10px) scale(0.75);
  transform: translateY(-30px) translateX(10px) scale(0.75);
}

.input:not(:placeholder-shown) ~ .placeholder {
  color: #808097;
}

.input:focus ~ .placeholder {
  color: #dc2f55;
}

.submit {
  background-color: #d4e162;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: black;
  font-weight: 700;
  cursor: pointer;
  font-size: 18px;
  height: 50px;
  margin-top: 38px;
  text-align: center;
  width: 100%;
}

@media (max-width: 1024px) {
  .form {
    width: 87%;
  }
}

:root {
  /* Primary */
  --Red: hsl(0, 78%, 62%);
  --Cyan: hsl(180, 62%, 55%);
  --Orange: hsl(34, 97%, 64%);
  --Blue: hsl(212, 86%, 64%);
  /* Neutral */
  --Very-Dark-Blue: hsl(234, 12%, 34%);
  --Grayish-Blue: hsl(229, 6%, 66%);
  --Very-Light-Gray: hsl(0, 0%, 98%);
}

*{
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  font-size: 0.9rem;
  color: hsl(234, 12%, 34%);
  color: var(--Very-Dark-Blue);
  background-color: black;
  flex-wrap: wrap;
  min-height: 100vh;
  max-height: -webkit-max-content;
  max-height: max-content;
  grid-gap: 20px;
  gap: 20px;
  padding: 30px;
  min-width: 98vw;
  max-width: 98vw;
}

.feature-one {
  border-top: 5px solid #94E8B4;
}

.feature {
  position: relative;
  width: 20rem;
  height: 15rem;
  margin: 2rem auto;
  padding: 2em;
  border-radius: 0.75em;
  box-shadow: 5px 5px 20px rgba(0 0 0/0.15);
  background-color: white;
  text-align: left;
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.feature__title{
  font-size: 1.3rem;
  font-weight: 600;
}

.feature:hover {
  -webkit-transform: scale(1.03);
  transform: scale(1.03);
}

.feature__desc {
  margin-top: 0.5em;
  color: hsl(229, 6%, 66%);
  color: var(--Grayish-Blue);
}

.feature__img {
  position: absolute;
  bottom: 10%;
  right: 10%;
}

.Model {
  position: absolute;
  inset: 0% 0% 0% 0%;
  background-color: #1e1e1e5a;
  display: grid;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 98vw;
  z-index: 111111111;
}

.Model_box {
  background-color: #0B0B0B;
  padding: 2rem;
  border-radius: 0.5rem;
}

.Model_box_heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Model_box_heading p {
  font-size: 1.2rem;
}

.Model_box_wallet p {
  background-color: #6b6dff;
  padding: 1rem;
  border-radius: 0.5rem;
  color: #1e1e1e;
  font-weight: 700;
  cursor: pointer;
}

.Model_box_para {
  padding: 1rem;
  line-height: 1.2;
  color: rgb(255, 82, 82);
  /* border: 1px solid #6b6dff; */
  border-radius: 0.5rem;
}

.inp {
  border: 1px solid #D0E167;
}

.map-container{
  width: 100%;
  height: 100vh;
}
